import { isPlatformBrowser } from '@angular/common';
import { Component, Inject, PLATFORM_ID } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { APP_ROUTE } from '@core/constants';
import { AuthService } from '@core/data-access/services/auth.service';
import { ProfileState } from '@core/data-access/states/profile.state';
import { SnackService } from '@core/services/snack.service';
import { AppEmailValidator } from '@core/validators';
import { ButtonType } from '@shared/components/button/button.component';
import { AgentFacade } from './agent.facade';
import { EnquirySentDialogComponent } from './components/enquiry-sent-dialog/enquiry-sent-dialog.component';

// TODO: This component is not being used anymore. The new component is `AgentHubLandingComponent`.
@Component({
    selector: 'findqo-agent',
    templateUrl: './agent.component.html',
    styleUrls: ['./agent.component.scss'],
    standalone: false
})
export class AgentPage {
	constructor(
		@Inject(PLATFORM_ID) private readonly platformId: string,
		private readonly fb: FormBuilder,
		private readonly agentFacade: AgentFacade,
		private readonly dialog: MatDialog,
		private readonly snackService: SnackService,
		private readonly authService: AuthService,
		private readonly profileState: ProfileState,
		private readonly router: Router,
		private readonly auth: AuthService
	) {}

	form: FormGroup = this.fb.group({
		name: ['', [Validators.required, Validators.maxLength(80), Validators.minLength(3)]],
		email: ['', [Validators.required, AppEmailValidator.validateEmailPattern()]],
		phone: ['', [Validators.required, Validators.maxLength(10), Validators.pattern('^[0-9]*$')]],
	});

	displayedColumns: string[] = ['subscriptionPlan', 'includes', 'monthlyCost'];
	readonly buttonType = ButtonType;
	readonly isPlatformBrowser = isPlatformBrowser(this.platformId);
	dataSource = {
		planName: 'Premium Unlimited',
		planDuration: '12 Months Unlimited',
		includes: ['n4085', 'n3897', 'n3334', 'n3888', 'n3889', 'n3330', 'n3890', 'n3891', 'n3892', 'n3893'],
		monthly: {
			currentCost: '135',
			originalCost: '€295',
		},
		yearly: {
			currentCost: '995',
			originalCost: '€1620',
		},
		currency: '€',
		savings: '625',
	};
	isMonthly = true;

	onSubscribe(): void {
		const IS_AGENT = this.auth.user()?.roles.some((role) => role === 'isAgentUser');

		if (IS_AGENT) {
			this.snackService.open({
				data: { message: 'You are not subscribed to any plan, please contact your business administrator' },
				panelClass: ['snackbar-error'],
			});

			return;
		}

		const IS_LOGGED_IN = this.authService.user()?.accessToken;

		const navigateTo = () => {
			const route = this.profileState.getUser()?.hasBusinessProfile === '0' ? APP_ROUTE.agentHub.landing : APP_ROUTE.agentHub.businessAccountSubscription;
			this.router.navigate([route]);
		};

		if (IS_LOGGED_IN) {
			navigateTo();
		} else {
			this.authService.openAuthDialog({ onLogin: navigateTo });
		}
	}

	onSubmit(): void {
		this.agentFacade.singupAgent(this.form.value).subscribe(
			() => {
				this.form.reset();
				this.form.get('name').setErrors(null);
				this.form.get('email').setErrors(null);
				this.form.get('phone').setErrors(null);

				this.dialog.open(EnquirySentDialogComponent, {
					width: '100%',
					maxWidth: '500px',
					height: 'auto',
				});
			},
			() => {
				this.snackService.open({
					data: { translationKey: 'n608' },
					panelClass: ['snackbar-error'],
				});
			}
		);
	}
}
