import { Injectable } from '@angular/core';
import { AgentsApi } from '@core/data-access/apis/agents.api';
import { AgentSignUpBody } from '@core/models';
import { Observable } from 'rxjs';

@Injectable({
	providedIn: 'root',
})
export class AgentFacade {
	constructor(private readonly agentApi: AgentsApi) {}

	singupAgent(body: AgentSignUpBody): Observable<object> {
		return this.agentApi.agentSignUp(body);
	}
}
